import request from './index.js'
import testRequest from './testIndex.js'

const api = {
    // 
    // login(data){
    //     // return request.post('/auth_jwt/',data)
    //     return testRequest.post('/login',data)
    // },
    //
    logout(data){
        // return request.post('')
    },
    // getESPCustInfo(){
    //     return testRequest.post('/getESPCustInfo')
    // },
    //
    getCustomerInfoList(){
        return request.get('/accounts')
    },
    //
    getCustInfoAccCondition(params){
        return request.get('/accounts', params)
    },
    getDictionary(dictionaryName){
        return request.get('/dictionary/' + dictionaryName)
    },
    //
    getCreateCustomer(){
        return request.get('/accounts/create')
    },
    // //
    // getDetailCustomer(id){
    //     return request.get('/accounts/' + id)
    // },
    ///
    getPreAndNextCustInfo(id,params){
        return request.get('/accounts/' + id, params)
    },
    //
    addCustomerInfo(data){
        return request.post('/accounts', data)
    },
    //
    deleteCustomerInfo(id){
        return request.delete('/accounts/'+id)
    },
    //
    editCustomerInfo(id,data){
        return request.patch('/accounts/'+id, data)
    },
    //
    batchUpdate(data){
        return request.patch('/accounts/batchupdate',data)
    },
    //
    batchDelete(params){
        return request.delete('/accounts/batchdelete',params)
    },
    //
    getTelemarketingInfo(){
        return request.get('/accounts/telemarketing')
    },
    //
    getHandoutList(data){
        return request.post('/handout/handout/getcount',data)
    },
    //
    handoutList(data){
        return request.post('/handout/handout/change',data)
    },
    //
    deleteList(data){
        return request.post('/handout/handout/delete',data)
    },
    //
    getTrackRecord(){
        return request.get('/accounttrack')
    },
    //
    getCaculateRecords(){
        return request.get('/caculaterecords')
    }
}

export default api


